* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  background-image: url("./images/background1.jpg");
  background-position: center;
  background-size: auto;
  background-attachment: fixed;
  min-height: 100vh;
}

div.content {
  display: flex;
  flex-direction: column;
  align-items: center;
}
